import React from 'react';
import classNames from 'classnames';
import { Link } from 'react-router-dom';

const Logo = ({
  className,
  ...props
}) => {

  const classes = classNames(
    'brand',
    className
  );

  return (
    <div
      {...props}
      className={classes}
      style={{ display: 'flex', justifyContent: 'center', width: '100%'}}
    >
      <Link to="/">

        <img
          src={require('./../../../assets/images/avatar.png')}
          alt="Open"
          width={100}
          style={{ borderRadius: '100%', marginTop: 120, marginBottom: 50, border: 'solid 2px #FFF'}}
          height={100} />
      </Link>
    </div>
  );
}

export default Logo;